import { Component } from '@angular/core';
import { ChartConst } from 'O2-chart-lib'; // <= Add

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app works!';

    chartType:string;
    configData:any;
    barDataJson:any;
    barTypeName:string;

  public slides = [
    {
       image: 'http://lorempixel.com/900/500?r=1',
       text: 'スライド１'
    },
    {
       image: 'http://lorempixel.com/900/500?r=2',
       text: 'スライド２'
    },
    {
       image: 'http://lorempixel.com/900/500?r=3',
       text: 'スライド３'
    },
  ];

    constructor() {
        this.barTypeName            = ChartConst.BAR_CHART_TYPE_NAME;
        this.initilizeData();   
    }

    private initilizeData(){
        this.configData ={
            "className":{
                "axis":"axis",
                "axisXBorder":"axis_x",
                "axisXText":"axis-x-text",
                "bar":"bar",
                "barValue":"bar-value",
                "line":"line",
                "multiLinePrefix":"line-",
                "grid":"grid",
                "pie":"pie",
                "pieInnerTitle": "pie-inner-title",
                "pieInnerRadius":"total",
                "histogram":"histogram",
                "histogramBar":"histogram-bar",
                "treemap":"treemap",
                "treemapLabel":"treemap-label",
                "packlayout":"packlayout",
                "packlayoutLabel":"packlayout-label",
            },
            "label": {
                    "display":true,
            },
            "title": {
                "display": true,
                "name":"Title",
                "className":"chart-title",
                "height":30,
                "leftMargin":-20,
                "bottomMargin":10
            },
            "maxValue":{
                "auto":true,
                "x":100,
                "y":100,
            },
            "legend": {
                "display": true,
                "position": "right",
                "totalWidth":80,
                "initXPos":5,
                "initYPos":10,
                "rectWidth":10,
                "rectHeight":10,
                "xSpacing":2,
                "ySpacing":2
            },
            "color":{
                "auto":true, // 
                "defaultColorNumber":10,
                "opacity":1.0,
                "userColors":[
                    "blue",
                    "red",
                    "green",
                    "yellow",
                    "PaleGoldenrod",
                    "Khaki",
                    "DarkKhaki",
                    "Gold",
                    "Cornsilk",
                    "BlanchedAlmond",
                    "Bisque",
                    "NavajoWhite",
                    "Wheat",
                    "BurlyWood",
                    "Tan",
                    "RosyBrown",
                    "SandyBrown",
                    "Goldenrod",
                    "DarkGoldenrod",
                    "Peru",
                    "Chocolate"
                ],
                "focusColor":"red",
            },
            "pie":{
                "innerRadius": {
                    "percent":20,
                    "title":"Total"
                },
                "value":{
                    "display":true,
                },  
                "percent":{
                    "display":false,
                }   
            },  
            "line": {
                "legend":"lineEnd",
                "interpolate" :"linear",
            },
            "grid":{
                "x":{
                    "display":true,
                },
                "y":{
                    "display":true,
                },
            },
            "margin":{
                "top":30,
                "left":30,
                "right":10,
                "bottom":20,
                "between":5
            },
            "axis":{
                "rotation":0,
                "borderLineWidth":1,
                "xLabel":{
                    "leftMargin":0,
                    "bottomMargin":5
                },
                "yLabel":{
                    "leftMargin":0,
                    "bottomMargin":0
                },

            },
            "animation":{
                "enable":true,
                "duration":4000,
            },  
        };

        this.barDataJson =
        {
            "series":[
                "English",
                "Math"  
            ],
            "data":[
                {
                    "x": "suzuki",
                    "y": [92,73],
                },
                {
                    "x": "inoue",
                    "y": [69,45],
                },
                {
                    "x": "sato",
                    "y": [70,100],
                },
                {
                    "x": "tanaka",
                    "y": [43,66],
                },
                {
                    "x": "ida",
                    "y": [60,70],
                },
                {
                    "x": "kato",
                    "y": [55,63],
                },
            ],
        };
  }

  // Add End --------------------------------------------




}



